import React from "react";
import { useState, useEffect } from "react";
import { WebRenderer } from "@wharfkit/web-renderer";
import { TransactPluginResourceProvider } from "@wharfkit/transact-plugin-resource-provider";
import { WalletPluginAnchor } from "@wharfkit/wallet-plugin-anchor";
import { WalletPluginCloudWallet } from "@wharfkit/wallet-plugin-cloudwallet";
import { Chains, SessionKit } from "@wharfkit/session";

const Login = () => {
  const anchor = new WalletPluginAnchor();
  const wcw = new WalletPluginCloudWallet();
  const chains = [Chains.WAX];
  const skoptions = {
    transactPlugins: [new TransactPluginResourceProvider()],
  };
  // Create a new session kit instance
  const sessionKit = new SessionKit(
    {
      appName: "reg-app",
      chains,
      ui: new WebRenderer(),
      walletPlugins: [wcw, anchor],
    },
    skoptions
  );

  const [session, setSession] = useState();
  async function login() {

    const response = await sessionKit.login();
    //console.log(response.session);
    setSession(response.session);
    //localStorage.setItem('session', response.session.serialize());
    localStorage.setItem(
      "session",
      JSON.stringify(response.session.serialize())
    );

    localStorage.setItem(
      "wax_address",
      JSON.stringify(response.session.permissionLevel.actor.toString())
    );

    window.location.reload();
  }
  async function restoreSession() {
    const restoredSession = await sessionKit.restore();
    //console.log(restoredSession.serialize().actor);
    setSession(restoredSession);
    if (
      restoredSession &&
      restoredSession.permissionLevel &&
      restoredSession.permissionLevel.actor !== undefined
    ) {
      //console.log(restoredSession.serialize());
      localStorage.setItem(
        "session",
        JSON.stringify(restoredSession.serialize())
      );
      localStorage.setItem(
        "wax_address",
        JSON.stringify(restoredSession.permissionLevel.actor.toString())
      );
    }
    //console.log(JSON.parse(localStorage.getItem("session")));
  }
  async function logout() {
    await sessionKit.logout(session);
    localStorage.clear();
    window.location.reload();
  }
  useEffect(() => {
    restoreSession();

  }, []); // The empty array ensures this runs on mount only
  return (
    <div>
      {session ? (
        <button onClick={logout}>Logout [{session.serialize().actor}]</button>
      ) : (
        <button onClick={login}>Login</button>
      )}
    </div>
  );
};

export default Login;
