import logo from "./OIG4_2.jpg";
import "./App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import React, { useState, useEffect } from "react";
import Login from "./components/Login"; // Adjust the path as necessary
//import Send from "./components/Send";

import { getDatabase, ref, onValue, remove } from "firebase/database";
import { set } from "firebase/database";
import { initializeApp } from "firebase/app";
import { initializeAppCheck, ReCaptchaV3Provider } from "firebase/app-check";

//Create a WAX session
import { WebRenderer } from "@wharfkit/web-renderer";
import { TransactPluginResourceProvider } from "@wharfkit/transact-plugin-resource-provider";
import { WalletPluginAnchor } from "@wharfkit/wallet-plugin-anchor";
import { WalletPluginCloudWallet } from "@wharfkit/wallet-plugin-cloudwallet";
import { Chains, SessionKit } from "@wharfkit/session";

const firebaseConfig = {
  apiKey: "AIzaSyBrqaJtGKeHQwAOxYK4bg5CgY6A0wE-2FU",
  authDomain: "moddedcraft-57b93.firebaseapp.com",
  databaseURL: "https://moddedcraft-57b93-default-rtdb.firebaseio.com",
  projectId: "moddedcraft-57b93",
  storageBucket: "moddedcraft-57b93.appspot.com",
  messagingSenderId: "311805948926",
  appId: "1:311805948926:web:15f139984755d3ea1286af",
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);

const appCheck = initializeAppCheck(app, {
  provider: new ReCaptchaV3Provider("6Lfj5P4pAAAAAA0atrb86CcJbb5OwYo0FhoiXuDg"),
  isTokenAutoRefreshEnabled: true,
});

const database = getDatabase(app);
//console.log(appCheck);
const collectionRef = ref(database, "/users");

if (collectionRef !== undefined) {
  onValue(collectionRef, (snapshot) => {
    const dataItem = snapshot.val();
    //console.log(dataItem);
  });
}

const anchor = new WalletPluginAnchor();
const wcw = new WalletPluginCloudWallet();
const chains = [Chains.WAX];
const skoptions = {
  transactPlugins: [new TransactPluginResourceProvider()],
};
// Create a new session kit instance
const sessionKit = new SessionKit(
  {
    appName: "reg-app",
    chains,
    ui: new WebRenderer(),
    walletPlugins: [wcw, anchor],
  },
  skoptions
);

function App() {
  const [username, setUsername] = useState("");
  let [isRegistered, setIsRegistered] = useState(false);
  let [accountDetails, setAccountDetails] = useState("");

  const [session, setSession] = useState();
  let [wlplayers, setWlplayers] = useState([]);
  let [transferMio, setTransferMio] = useState(false);


  async function registerUser(username) {
    const waxAddress = JSON.parse(localStorage.getItem("wax_address"));

    if (waxAddress === null) {
      return false;
    }
    let check_trans = await get_transfer_info(session.serialize().actor);
    if (!check_trans) {
      return false;
    }
    const collectionRef = ref(database, "/users");

    if (collectionRef !== undefined) {
      onValue(collectionRef, (snapshot) => {
        const dataItem = snapshot.val();
        console.log(dataItem);
        setWlplayers(dataItem);
      });
    }

    const sanitizedItem = waxAddress.replace(/\./g, "_");

    let uuid = await getUuid(username);

    const playerFound = searchByMCUUID(wlplayers, uuid);

    if (playerFound) {
      console.log("Player found:", playerFound);
      return;
    } else {
      console.log(
        "No player found with the specified MCUUID. All set to register."
      );
    }

    if (uuid.id !== null) {
      localStorage.setItem("mcusername", username);

      set(ref(database, "users/" + sanitizedItem), {
        username: sanitizedItem,
        waxaddress: waxAddress,
        mcusername: username,
        mcuuid: uuid.id,
      });
      isRegistered = true;
      localStorage.setItem("is_registered", JSON.stringify(isRegistered));
      localStorage.setItem("mcuuid", JSON.stringify(uuid.id));
      setIsRegistered(isRegistered);

      const userDetails = {
        username: sanitizedItem,
        waxaddress: waxAddress,
        mcusername: username,
        mcuuid: uuid.id,
      };

      const userDetailsString = JSON.stringify(userDetails);

      localStorage.setItem("userDetails", userDetailsString);
      setAccountDetails(userDetails);
    }
    //window.location.reload();
  }

  async function getUuid(item) {
    let mojang_info = await fetch("https://api.minetools.eu/uuid/" + item);
    let uuid_dat = await mojang_info.json();
    console.log(uuid_dat);
    return uuid_dat;
  }
  async function formatUUID(uuid) {
    return `${uuid.slice(0, 8)}-${uuid.slice(8, 12)}-${uuid.slice(
      12,
      16
    )}-${uuid.slice(16, 20)}-${uuid.slice(20)}`;
  }
  async function formatAndCollectUUID(uuid) {
    return await formatUUID(uuid);
  }

  async function removeUser(wax_address) {
    localStorage.setItem("is_registered", JSON.stringify(false));
    localStorage.setItem("mcuuid", JSON.stringify(null));
    if (wax_address === null) {
      return false;
    }
    const sanitizedItem = wax_address.replace(/\./g, "_");
    remove(ref(database, "users/" + sanitizedItem));
    window.location.reload();
  }

  function searchByMCUUID(players, mcuuid) {
    // Convert the players object to an array of player objects
    const playerArray = Object.values(players);
    console.log(playerArray);
    console.log(mcuuid);
    // Search for the player with the specified mcuuid
    const foundPlayer = playerArray.find(
      (player) => player.mcuuid === mcuuid.id
    );

    // Return the found player or null if not found
    return foundPlayer || null;
  }

  const downloadJson = async () => {
    // Convert dataItem to a JSON string
    const playerArray = Object.values(wlplayers);
    const simplifiedPlayers = await Promise.all(
      playerArray.map(async (player) => ({
        uuid: await formatAndCollectUUID(player.mcuuid),
        name: player.mcusername,
      }))
    );

    const jsonString = JSON.stringify(simplifiedPlayers);
    // Create a Blob from the JSON string
    const blob = new Blob([jsonString], { type: "application/json" });
    // Create a URL for the blob
    const url = URL.createObjectURL(blob);
    // Create a temporary link element
    const link = document.createElement("a");
    link.download = "whitelist.json";
    link.href = url;
    // Append the link to the body
    document.body.appendChild(link);
    // Trigger the download by simulating a click on the link
    link.click();
    // Clean up by removing the temporary link
    document.body.removeChild(link);
  };

  async function get_transfer_info(name) {
    let check_transfer = await fetch(
      "https://wax.api.atomicassets.io/atomicassets/v1/transfers?sender=" +
        name +
        "&recipient=moddedwax.gm&template_id=235703&after=1713951259500&page=1&limit=100&order=desc&sort=created"
    );

    let transfer_dat = await check_transfer.json();
    //console.log(transfer_dat);
    if (transfer_dat.data.length > 0) {
      //console.log("Transfer found");
      setTransferMio(true);
      return true;
    }
    //console.log("No transfer found");
    setTransferMio(false);
    return false;
  }

  async function checkIfRegistered() {
    const session = await sessionKit.restore();
    if (!session) {
      return false;
    }

    setSession(session);
    //console.log(session.serialize().actor);

    const sanitizedName = session.serialize().actor.replace(/\./g, "_");
    const collectionRef = ref(database, "/users");
    if(session){
      get_transfer_info(session.serialize().actor);
      }
    if (collectionRef !== undefined) {
      onValue(collectionRef, (snapshot) => {
        const dataItem = snapshot.val();
        //console.log(dataItem);
        setWlplayers(dataItem);

        if (dataItem !== null && dataItem[sanitizedName] !== undefined) {
          isRegistered = true;
          localStorage.setItem("is_registered", JSON.stringify(isRegistered));
          localStorage.setItem(
            "mcuuid",
            JSON.stringify(dataItem[sanitizedName].mcuuid)
          );
          localStorage.setItem(
            "mcusername",
            dataItem[sanitizedName].mcusername
          );

          const userDetails = {
            username: sanitizedName,
            waxaddress: session.serialize().actor,
            mcusername: dataItem[sanitizedName].mcusername,
            mcuuid: dataItem[sanitizedName].mcuuid,
          };

          const userDetailsString = JSON.stringify(userDetails);

          localStorage.setItem("userDetails", userDetailsString);
          setAccountDetails(userDetails);
          setIsRegistered(isRegistered);

          //console.log(JSON.parse(localStorage.getItem("userDetails")).mcuuid);
        } else {
          //console.log("Not registered");
          isRegistered = false;
          localStorage.setItem("mcuuid", null);
          localStorage.setItem("mcusername", null);
          localStorage.setItem("userDetails", null);
        }
      });
    }

    return isRegistered;
  }

  useEffect(() => {

    checkIfRegistered();
   
  }, []);
  return (
    <div className="App">
      <header className="App-header">
        <Login />
        <img src={logo} className="App-logo" alt="logo" />

        {session && (
          <div>
            <div>
              {!isRegistered && (
                <div>
                  <div>
                    <span className="badge bg-secondary">Not registered</span>
                  </div>
                  <div>
                    <div>
                      {transferMio ? <span> ✔️</span> : <span style={{color: 'red'}}> ❌</span>}Send 1 Million 
                      Crystallized Upliftium NFT to moddedwax.gm
                     </div>
                     <div>
                     <span style={{color: 'red'}}> ❌</span>Register your Minecraft username.
                        </div>
                  </div>
                  <div>
                    <input
                      type="text"
                      value={username}
                      onChange={(e) => setUsername(e.target.value)}
                      placeholder="Enter username"
                    />
                  </div>

                  <button onClick={() => registerUser(username)}>
                    Register
                  </button>
                </div>
              )}

              {JSON.parse(localStorage.getItem("userDetails")) !== null && (
                <div>
                  {" "}
                  <button
                    onClick={() =>
                      removeUser(
                        JSON.parse(localStorage.getItem("userDetails"))
                          .waxaddress
                      )
                    }
                  >
                    Unregister
                  </button>
                  <div style={{ fontWeight: "bold", fontSize: "medium" }}>
                    <div>
                      <img
                        src={
                          "https://crafthead.net/avatar/" +
                          (JSON.parse(
                            localStorage.getItem("userDetails")
                          ).mcuuid.replace(/"/g, "") || "")
                        }
                        alt="Minecraft Avatar"
                        width="50"
                        height="50"
                      />

                      {accountDetails && (
                        <div>
                          {(() => {
                            return (
                              <div>
                                <p>WAX Address: {accountDetails.waxaddress}</p>
                                <p>
                                  Minecraft Username:{" "}
                                  {accountDetails.mcusername}
                                </p>
                              </div>
                            );
                          })()}
                        </div>
                      )}
                    </div>
                  </div>
                </div>
              )}
            </div>
            <div className="d-flex justify-content-center align-items-center">
              {wlplayers ? (
                <table className="table table-bordered text-center">
                  <thead className="fw-bold">
                    <tr>
                      <th></th>
                      <th>MC Username</th>
                      <th>WAX Address</th>
                    </tr>
                  </thead>
                  <tbody>
                    {Object.entries(wlplayers).map(([key, player]) => (
                      <tr key={key}>
                        <td>
                          {" "}
                          <img
                            src={
                              "https://crafthead.net/avatar/" +
                              (player.mcuuid.replace(/"/g, "") || "")
                            }
                            alt="Minecraft Avatar"
                            width="25"
                            height="25"
                          />
                        </td>
                        <td>{player.mcusername}</td>
                        <td>{player.waxaddress}</td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              ) : (
                <div>No players found.</div>
              )}
            </div>
          </div>
        )}
      </header>

      <div>
        <button onClick={downloadJson}>Download Whitelist</button>
      </div>
    </div>
  );
}

export default App;
